.container {
    min-height: calc(100vh - 132px);
    margin-top: 64px;
}

.paymentPage {
    max-width: 1200px;
    margin: 10px auto;
    /* Added 10px margin around the page */
    padding: 40px 20px;
    background-color: white;
    border-radius: 8px;
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
}

.steps {
    display: flex;
    justify-content: space-between;
    margin-bottom: 20px;
}

.step {
    flex: 1;
    text-align: center;
    font-weight: bold;
    color: #ccc;
    padding-bottom: 10px;
    border-bottom: 2px solid #ddd;
}

.step span {
    margin-right: 5px;
}

.active {
    color: #ff9900;
    border-bottom: 2px solid #ff9900;
}

.mainContainer {
    display: flex;
    justify-content: space-between;
}

.leftSide {
    width: 60%;
    padding-right: 20px;
}

.rightSide {
    width: 35%;
    margin-top: 36px;
}

.formGroup {
    margin-bottom: 20px;
}

.formGroup label {
    display: block;
    margin-bottom: 5px;
    font-weight: bold;
}

.formGroup input,
.formGroup select,
.react-datepicker-wrapper input {
    width: 100%;
    padding: 10px;
    border: 1px solid #ccc;
    border-radius: 5px;
}

.react-datepicker-wrapper {
    width: 100%;
}

.selectField {
    min-width: 100%;
}

.ticketSelection {
    margin-bottom: 20px;
}

.ticketCounter {
    display: flex;
    align-items: center;
    padding: 10px;
    border-radius: 8px;
}

.ticketButton {
    background-color: #ff9900;
    color: white;
    border: none;
    padding: 10px 15px;
    border-radius: 5px;
    font-size: 16px;
    cursor: pointer;
}

.ticketButton:hover {
    background-color: #e68a00;
}

.ticketCount {
    font-size: 18px;
    margin: 0 15px;
}

.ticketPrice {
    margin-top: 10px;
    font-weight: bold;
}

.summarySection {
    padding: 20px;
    background-color: #fff;
    border-radius: 8px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.summarySection h4 {
    margin-bottom: 20px;
}

.ticketDetails p {
    margin-bottom: 10px;
}

.ticketDetails hr {
    margin: 20px 0;
}

.totalPrice {
    font-size: 20px;
    font-weight: bold;
    color: #ff9900;
}

.nextStepBtn {
    background-color: #ff9900;
    color: white;
    padding: 15px;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    width: 100%;
    font-size: 16px;
}

.nextStepBtn:hover {
    background-color: #e68a00;
}

/* Order Confirmation Page CSS */
.confirmationPage {
    text-align: center;
    padding: 50px;
    background-color: white;
    margin: 100px auto;
    border-radius: 10px;
    max-width: 600px;
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
}

.iconContainer {
    font-size: 50px;
    color: #ff9900;
    margin-bottom: 20px;
}

.icon {
    font-size: 50px;
}

h1 {
    font-size: 24px;
    font-weight: bold;
    margin-bottom: 10px;
}

p {
    font-size: 16px;
    color: #666;
}

/* Responsive Design */
@media screen and (max-width: 768px) {
    .mainContainer {
        flex-direction: column;
    }

    .leftSide,
    .rightSide {
        width: 100%;
    }

    .rightSide {
        margin-top: 20px;
    }
}

/* Additional Media Query for iPhone XR and Similar Devices (828px width) */
@media screen and (max-width: 828px) {
    .paymentPage {
        margin: 10px;
        /* Maintain 10px margin on smaller screens */
        padding: 20px 10px;
    }

    .steps {
        flex-direction: column;
    }

    .step {
        margin-bottom: 15px;
    }

    .mainContainer {
        flex-direction: column;
        align-items: center;
    }

    .leftSide,
    .rightSide {
        width: 100%;
        padding: 0;
    }

    .ticketCounter {
        flex-direction: column;
        align-items: flex-start;
    }

    .ticketButton {
        width: 100%;
        margin-bottom: 10px;
    }

    .ticketCount {
        margin: 10px 0;
        font-size: 1.5rem;
    }

    .summarySection {
        width: 100%;
        margin-top: 20px;
    }

    .nextStepBtn {
        font-size: 1.1rem;
        padding: 12px;
    }
}

/* For even smaller screens (like iPhone SE or small Android devices) */
@media screen and (max-width: 575px) {
    .paymentPage {
        margin: 10px;
        /* Maintain 10px margin for mobile screens */
        padding: 15px;
    }

    .steps {
        flex-direction: column;
        text-align: center;
    }

    .step {
        margin-bottom: 10px;
    }

    .ticketCount {
        font-size: 1.2rem;
    }

    .nextStepBtn {
        font-size: 1rem;
        padding: 10px;
    }

    .confirmationPage {
        margin: 50px auto;
        padding: 30px;
    }

    .iconContainer {
        font-size: 40px;
    }

    h1 {
        font-size: 20px;
    }

    p {
        font-size: 14px;
    }
}